@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.maletin{margin:0.2em;padding:0.2em;border-radius:5px;text-align:center;position:relative;left:0;-webkit-transition:left 1s linear;transition:left 1s linear}.maletin span{line-height:0.5em;font-weight:bolder;font-family:Arial, Helvetica, sans-serif;font-size:20px;color:White;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.maletin img{width:50px;height:50px}.maletin:hover{background-color:#ffd700;cursor:pointer}@media screen and (max-width: 700px){.maletin{padding:0.1em}.maletin span{font-size:15px}}

.maletines div{display:flex;flex-wrap:wrap;text-align:center;justify-content:center}

.display-seleccionado{margin:0 auto;text-align:center;-webkit-transition:ease-in 1s;transition:ease-in 1s}.display-seleccionado span{font-family:Arial, Helvetica, sans-serif;font-style:oblique;font-weight:lighter;text-align:center;color:Grey}.seleccionado{background-color:gold;width:70px;height:70px;padding:1em;border-radius:50%;box-shadow:0px 10px 15px 3px rgba(0,0,0,0.5);margin:1em auto}.seleccionado span{color:white;font-style:unset}.seleccionado img{margin-top:0.4em}.seleccionado:hover{cursor:unset}

.tracker{background-color:grey;width:100%;height:2px;display:flex}.bar-completed{background-color:chartreuse;height:100%;max-width:100%}.segment{height:100%;flex-grow:1;position:relative}.bar{height:100%}.segment-completed{height:10px;width:10px;border-radius:50%;position:absolute;z-index:99;top:50%;right:0;-webkit-transform:translate(50%, -50%);transform:translate(50%, -50%);background-color:white;border:solid 2px;box-sizing:border-box}.segment-completed-filled{background-color:crimson;border:none}

.contraoferta{display:flex;margin-top:0.2;flex-direction:column;align-items:center}.contraoferta input{width:50%}.contraoferta input[type="range"]{width:90%}.contraoferta .contra-oferta-valor{font-size:20px}.notificacion{padding:1em;margin:1em;min-width:280px;background-color:slategrey;text-align:center}.notificacion div{margin-top:1em}.notificacion span{color:White;font-family:Arial, Helvetica, sans-serif}.notificacion h2{color:white;margin:0}.notificacion input{margin:0.5em}

.notificaciones{position:fixed;bottom:0}.step-enter{-webkit-transform:translateY(500px);transform:translateY(500px)}.step-enter.step-enter-active{-webkit-transition-property:-webkit-transform;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;-webkit-transition-duration:300ms;transition-duration:300ms;-webkit-transition-timing-function:ease;transition-timing-function:ease;-webkit-transform:translateY(0);transform:translateY(0)}.step-exit{-webkit-transition-property:opacity;transition-property:opacity;-webkit-transition-duration:300ms;transition-duration:300ms;-webkit-transition-timing-function:ease;transition-timing-function:ease;opacity:1}.step-exit.step-exit-active{opacity:0}

.notificacion{padding:1em;margin:1em;min-width:280px;background-color:slategrey;text-align:center}.notificacion div{margin-top:1em}.notificacion span{color:White;font-family:Arial, Helvetica, sans-serif}.notificacion h2{color:white;margin:0}.notificacion input{margin:0.5em}

.body{margin:0;padding:0;min-height:100vh;height:100%}.game{display:flex;justify-content:space-between;flex-direction:row;position:relative;height:100%}.title{color:gold;font-family:'Pacifico', Arial, Helvetica, sans-serif}@media screen and (max-width: 700px){.title{margin-top:0.5em;font-size:18px}}.left{width:100%;box-sizing:border-box;min-height:100vh;height:100%;padding:3em;padding-top:0.2em}.left h2{text-align:center;font-weight:normal;color:#4d5b66;font-size:30px}.left span{text-align:center}.rigth{padding:0.7em;display:flex;z-index:999;flex-direction:column;width:200px;min-height:100vh;height:100%;box-shadow:0px 10px 15px 3px rgba(0,0,0,0.5);box-sizing:border-box}.valor{border-radius:5px;margin:0.2em;background-color:gold;padding:0.1em;text-align:center;font-size:small;font-weight:bold;color:grey;flex-grow:1;-webkit-transition-property:opacity color background-color;transition-property:opacity color background-color;-webkit-transition:linear opacity 2s;transition:linear opacity 2s}.valor-abierto{opacity:0.2;color:White;background-color:Grey}.trato-aceptado{text-align:center}.trato-aceptado h2{margin-bottom:0}.trato-aceptado span{font-weight:lighter}

